/* Container */
.container {
  box-sizing: border-box;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 2rem;
  padding-right: 2rem;
}

/** No gutters **/
.container-no-gutter {
  padding-left: 0;
  padding-right: 0;
  overflow-x: hidden;
}

.container-sm {
  max-width: 426px;
}

.container-md {
  max-width: 769px;
}

.container-lg {
  max-width: 1025px;
}

.container-xl {
  max-width: 1367px;
}

/* Grid */
.grid {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  margin-left: -0.5rem;
  margin-right: -0.5rem;
}

/* Direction */
.grid-row {
  flex-direction: row;
}

.grid-row-reverse {
  flex-direction: row-reverse;
}

/* Wrap */
.grid-wrap {
  flex-wrap: wrap;
}

.grid-no-wrap {
  flex-wrap: nowrap;
}

/* Gutters */
/** Large **/
.grid-lg-gutter {
  margin-left: -1.5rem;
  margin-right: -1.5rem;
}
.grid-lg-gutter > .cell {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

/** None **/
.grid-no-gutter {
  margin-left: 0;
  margin-right: 0;
}
.grid-no-gutter > .cell {
  padding-left: 0;
  padding-right: 0;
}

/* Justification X axis */
.grid-x-start {
  justify-content: flex-start;
}

.grid-x-center {
  justify-content: center;
}

.grid-x-end {
  justify-content: flex-end;
}

.grid-x-around {
  justify-content: space-around;
}

.grid-x-between {
  justify-content: space-between;
}

/* Justification Y axis */
.grid-y-stretch {
  align-items: stretch;
}

.grid-y-start {
  align-items: flex-start;
}

.grid-y-center {
  align-items: center;
}

.grid-y-end {
  align-items: flex-end;
}

/* Justification Y axis when wrapped: Z */
.grid-z-stretch {
  align-content: stretch;
}

.grid-z-start {
  align-content: flex-start;
}

.grid-z-center {
  align-content: center;
}

.grid-z-end {
  align-content: flex-end;
}

.grid-z-around {
  align-content: space-around;
}

.grid-z-between {
  align-content: space-between;
}

