/* Cell */
.cell {
  box-sizing: border-box;
  flex: 0 1 auto;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

/* Sizes */
.cell-1 {
  flex-basis: 8.3333333333%;
  max-width: 8.3333333333%;
}

.cell-2 {
  flex-basis: 16.6666666667%;
  max-width: 16.6666666667%;
}

.cell-3 {
  flex-basis: 25%;
  max-width: 25%;
}

.cell-4 {
  flex-basis: 33.3333333333%;
  max-width: 33.3333333333%;
}

.cell-5 {
  flex-basis: 41.6666666667%;
  max-width: 41.6666666667%;
}

.cell-6 {
  flex-basis: 50%;
  max-width: 50%;
}

.cell-7 {
  flex-basis: 58.3333333333%;
  max-width: 58.3333333333%;
}

.cell-8 {
  flex-basis: 66.6666666667%;
  max-width: 66.6666666667%;
}

.cell-9 {
  flex-basis: 75%;
  max-width: 75%;
}

.cell-10 {
  flex-basis: 83.3333333333%;
  max-width: 83.3333333333%;
}

.cell-11 {
  flex-basis: 91.6666666667%;
  max-width: 91.6666666667%;
}

.cell-12 {
  flex-basis: 100%;
  max-width: 100%;
}

/* Shrink */
.cell-shrink {
  flex-grow: 0;
  flex-basis: auto;
  max-width: none;
}

/* Grow */
.cell-grow {
  flex-grow: 1;
  flex-basis: auto;
  max-width: none;
}

/* No gutters */
.cell-no-gutter {
  padding-left: 0;
  padding-right: 0;
}

/* Gutters */
.cell-gutter {
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

[class*=grid-lg-gutter] > .cell-gutter {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

/* Visibility */
.cell-hidden {
  display: none;
}

.cell-visible {
  display: block;
}

/* Self justification Y axis */
.cell-y-stretch {
  align-self: stretch;
}

.cell-y-start {
  align-self: flex-start;
}

.cell-y-center {
  align-self: center;
}

.cell-y-end {
  align-self: flex-end;
}

/* Self justification X axis */
.cell-x-center {
  margin-left: auto;
  margin-right: auto;
}

.cell-x-start {
  margin-right: auto;
}

.cell-x-end {
  margin-left: auto;
}

/* Order */
.cell-first {
  order: -1;
}

.cell-last {
  order: 1;
}

.cell-ordered {
  order: 0;
}

