  .xl\:cell-1 {
    flex-basis: 8.3333333333%;
    max-width: 8.3333333333%;
  }

  .xl\:cell-2 {
    flex-basis: 16.6666666667%;
    max-width: 16.6666666667%;
  }

  .xl\:cell-3 {
    flex-basis: 25%;
    max-width: 25%;
  }

  .xl\:cell-4 {
    flex-basis: 33.3333333333%;
    max-width: 33.3333333333%;
  }

  .xl\:cell-5 {
    flex-basis: 41.6666666667%;
    max-width: 41.6666666667%;
  }

  .xl\:cell-6 {
    flex-basis: 50%;
    max-width: 50%;
  }

  .xl\:cell-7 {
    flex-basis: 58.3333333333%;
    max-width: 58.3333333333%;
  }

  .xl\:cell-8 {
    flex-basis: 66.6666666667%;
    max-width: 66.6666666667%;
  }

  .xl\:cell-9 {
    flex-basis: 75%;
    max-width: 75%;
  }

  .xl\:cell-10 {
    flex-basis: 83.3333333333%;
    max-width: 83.3333333333%;
  }

  .xl\:cell-11 {
    flex-basis: 91.6666666667%;
    max-width: 91.6666666667%;
  }

  .xl\:cell-12 {
    flex-basis: 100%;
    max-width: 100%;
  }

  /* Shrink */
  .xl\:cell-shrink {
    flex-grow: 0;
    flex-basis: auto;
    max-width: none;
  }

  /* Grow */
  .xl\:cell-grow {
    flex-grow: 1;
    flex-basis: auto;
    max-width: none;
  }

  /* No gutters */
  .xl\:cell-no-gutter {
    padding-left: 0;
    padding-right: 0;
  }

  /* Gutters */
  .xl\:cell-gutter {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }

  [class*=grid-lg-gutter] > .xl\:cell-gutter {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }

  /* Visibility */
  .xl\:cell-hidden {
    display: none;
  }

  .xl\:cell-visible {
    display: block;
  }

  /* Self justification Y axis */
  .xl\:cell-y-stretch {
    align-self: stretch;
  }

  .xl\:cell-y-start {
    align-self: flex-start;
  }

  .xl\:cell-y-center {
    align-self: center;
  }

  .xl\:cell-y-end {
    align-self: flex-end;
  }

  /* Self justification X axis */
  .xl\:cell-x-center {
    margin-left: auto;
    margin-right: auto;
  }

  .xl\:cell-x-start {
    margin-right: auto;
  }

  .xl\:cell-x-end {
    margin-left: auto;
  }

  /* Order */
  .xl\:cell-first {
    order: -1;
  }

  .xl\:cell-last {
    order: 1;
  }

  .xl\:cell-ordered {
    order: 0;
  }
}